/* 圆角 */
.rounded { border-radius: 8px;}
.rounded-top {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.rounded-right {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.rounded-bottom {
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
.rounded-left {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
 }
.rounded-circle { border-radius: 100px;}
.rounded-0 { border-radius: 0;}
